/* MovieList.module.css */

.styledSection {
    padding-right: 50px;
    padding-left: 50px;
  }
  
  .sectionTitle {
    margin-top: 30px;
    margin-bottom: 30px;
    padding-left: 5px;
    font-size: 36px;
    font-weight: 600;
    text-align: center;
  }
  
  .list {
    list-style: disc;
    margin: 30px;
    padding-left: 20px;
    color: #0d57aa;
  }
  
  .listItem:not(:last-child) {
    margin-bottom: 8px;
  }
  
  .linkItem {
    font-size: 22px;
    color: #0d57aa;
    transition: color 0.2s ease-in-out, border-bottom 0.2s ease-in-out;
  }
  
  .linkItem:hover,
  .linkItem:focus {
    color: #dc143c;
    border-bottom: 2px solid #1c85ff;
  }